	module.exports = {"data":{"markdownRemark":{"html":"<p>野沢プロト氏のWebコミック “最強クッキング” のヒロイン、ミラウーの金髪黒服バージョン。金髪黒服おにゃのこはステータス！とネット仲間で盛り上がっていた。 制作にはHyperPaintを使用。ペイントツールを使用しての画像制作はそれなりにやってきたが(例えばバナー制作とか)、キャラグラフィックをフル2DCGで作り上げるのはこれが初。  <img src=\"https://danmaq.com/wp-content/uploads/2015/05/42a2eeb26ebee778b4b18cb4ed7cce8a-443x1024.jpg\" alt=\"金髪黒服ミラウー(下書き)\"> ちなみに下書きはこんな感じ。当初からキャラグラフィックをCGで描くときは少なくとも紙上で下書きまでは進めてしまう。時期によってはアタリまで or クリナップまで or ペン入れまで、とまちまちだが少なくとも下書きまでは紙上でやっている。(だから未だに塗りが入ると極端に下手になるんだなｗ) しかし当時スキャナがなかったので下書きをCRTモニタに貼り付けてそれを元にアタリをつけていた。厳密に言うと EPSON の A5 スキャナを持っていたのだが、ジャンク屋で 600 円にて買ったもので、電源は入ったもののドライバとか公式ページにすら存在せずまともに読めなかった。 今思えば当時まだ髪留めの形状がよく分かってなかったせいか、ただ貼り付いているようにしか見えない。</p>","frontmatter":{"cover":null,"date":"1999-07-27T15:34:00+09:00","strDate":"1999/7/27","title":"[最強クッキング] ミラウー金髪黒服Ver."}}},"pathContext":{"path":"/ja/blog/1999/07/27-mirau/","langKey":"ja"}}

